:root {
    --section-background-color: #f5f5f5;
    --header-background-color: #fefefe;
    --yellow-color: #ffe550;
    --accent-green-color: #bbf330; 
    --accent-dark-green: #7eb101;
    --green-color: #B5FF00;
    --primary-text-color: #2c2c2c;
    --secondary-text-color: #000000;
    --error-color: #FF705D;
    --transition-time-func: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
